import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: () => import('@/components/Login'),
      hidden: true
    },
    {
      path: '/',
      name: '信息中心',
      component: () => import('@/components/Stage'),
      redirect: '/dashboard',
      meta: {
        requireLogin: true
      },
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: () => import('@/components/Dashboard'),
          hidden: true
        }
      ]
    },
    {
      path: '/mo',
      name: '京东钼媒',
      redirect: 'device/list',
      meta: {
        requireLogin: true
      },
      component: () => import('@/components/Stage'),
      children: [
        {
          path: 'device/list',
          name: '关联设备',
          component: () => import('@/components/mo/device/List')
        },
        {
          path: 'ads/list',
          name: '钼媒订单',
          component: () => import('@/components/mo/ads/Order')
        }
      ]
    },
    {
      path: '/jupingx',
      name: '百度聚屏',
      redirect: 'device/list',
      meta: {
        requireLogin: true
      },
      component: () => import('@/components/Stage'),
      children: [
        {
          path: 'device/list',
          name: '设备列表',
          component: () => import('@/components/mo/device/List')
        },
        {
          path: 'ads/list',
          name: '广告订单',
          component: () => import('@/components/mo/ads/Order')
        }
      ]
    }
  ]
})



